import React from 'react'
import {Link} from 'react-router-dom'
import './SvgCard.scss'

const SvgCard = (props) => {
    return (
        <div className={`svg-card ${props.styles}`} style={{ backgroundImage: `url(${props.bg})` }}
                data-aos={props.aos}>

            <div className="svg-section">
                {props.img && <div className='img' style={{ content: `url(${props.img})` }}></div>}
                {props.icon && (
                    <div className={`icon ${props.circle}`}>
                        {props.icon}
                    </div>
                )}
            </div>

            <div className="text-section">
                {props.title ? <h6>{props.title}</h6> : null}
                {props.description ? (
                    <p>{props.description}</p>
                ) : null}
                {props.to ? (
                    <Link to={props.to} className={props.linkStyles}>
                        {props.link}
                        {props.symbol}
                    </Link>
                ) : null}
            </div>
      </div>
    )
}

export default SvgCard
